.board {
  width: 40rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  border: 2px solid blue;
  background-color: blue;
}

.group {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  flex-wrap: wrap;
  padding: 0.3rem;
  border: 2px solid blue;
  border-radius: 6px;
  background-color: white;
}

.cell {
  display: grid;
  align-items: center;
  justify-content: center;
  aspect-ratio: 1/1;
  border-radius: 6px;
  margin: 0.3rem;
  cursor: pointer;
  box-shadow: 0px 0px 2px 0px rgba(0, 128, 0, 0.75);
}
.cell span {
  display: block;
  text-align: center;
}